main {
    .enjoy .container .grid-2 .row-1 p {
        max-width: 35ch;
    }

    .enjoy .container .grid-2 .row-2 {
        position: relative;
        z-index: 10;
    }

    .enjoy .container .grid-2 .row-2 picture {

        img {
            width: 100%;
        }

        video {
            position: absolute;
            overflow: hidden;
            width: 100%;
            height: 100%;
            max-width: 73%;
            max-height: 54%;
            top: 20%;
            left: 15%;
            z-index: -1;
        }
    }

    .everywhere .container .row-1 {
        position: relative;
        z-index: 10;

        img {
            width: 100%;
        }

        video {
            position: absolute;
            z-index: -1;
            overflow: hidden;
            width: 100%;
            height: 100%;
            max-width: 63%;
            max-height: 47%;
            top: 10%;
            left: 18%;
        }
    }


    .everywhere .container .row-2 {
        p {
            max-width: 40ch;
        }
    }

    .kids .container .row-1 {
        p {
            max-width: 40ch;
        }
    }

    .download .container .grid-2 .row-1 {
        position: relative;
    }

    .download .container .grid-2 .stranger {
        position: absolute;
        width: 60%;
        top: 70%;
        left: 20%;
        border: 2px solid rgba(255, 255, 255, 0.2);
        box-shadow: 0 0 2em 0 rgba(0, 0, 0, 1);
        border-radius: 0.75rem;
        z-index: 1;
    }

    .download .container .grid-2 .row-1 .column .row-1 {
        img {
            height: 4.5rem;
        }
    }

    @include lg {
        .enjoy .container .grid-2 .row-1 h2 {
            text-align: center;
        }

        .enjoy .container .grid-2 .row-1 p {
            text-align: center;
        }

        .everywhere .container .grid-2 .row-2 h2 {
            text-align: center;
        }

        .everywhere .container .grid-2 .row-2 p {
            text-align: center;
        }

        .everywhere .container .grid-2 .row-1 picture video {
            left: 80px;
        }

        .kids .container .grid-2 .row-1 h2 {
            text-align: center;
        }

        .kids .container .grid-2 .row-1 p {
            text-align: center;
        }

        .download .container .grid-2 .row-2 h2 {
            text-align: center;
        }

        .download .container .grid-2 .row-2 .only {
            text-align: center;
        }
    }
}